<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content">座位中心</block>
		</cu-custom>
		<scroll-view scroll-x class="bg-white nav text-center fixed" :style="[{top:CustomBar + 'px'}]">
			<view class="cu-item" :class="index==TabCur?'text-blue cur':''" v-for="(item,index) in tabNav" :key="index"
				@tap="tabSelect" :data-id="index">
				{{tabNav[index]}}
			</view>
		</scroll-view>
		<view class="bg-white padding">
			<view class="grid col-4 grid-square">
				<view class="bg-img" v-for="(item,index) in seatlist" :key="index">
					<view class="seattitle" @tap="todetail(item)">
						<view class="seattitle01">
							{{item.title}}
						</view>
						<view class="seattitle02">
							{{item.title}}
						</view>
					</view>
					
					<!-- <view class="seattitle03">
						预约时间：{{item.title}}
					</view> -->
				</view>
			</view>
		</view>
		<!-- <block v-if="TabCur==1">
			<view class="bg-white padding">
				<view class="grid col-4 grid-square">
					<view class="bg-img" v-for="(item,index) in avatar" :key="index"></view>
				</view>
			</view>
		</block>
		<block v-if="TabCur==2">
			<view class="bg-white padding">
				<view class="grid col-4 grid-square">
					<view class="bg-img" v-for="(item,index) in avatar" :key="index"></view>
				</view>
			</view>
		</block> -->

	</view>


</template>

<script>
	export default {
		mounted() {
			this.init()
		},
		data() {
			return {
				CustomBar: this.CustomBar,
				TabCur: 0,
				avatar: [],
				tabNav: [],
				seatlist:[]
			};
		},
		methods: {
			async init() {
				let that = this
				let msgobj = {}
				let resdata = await new this.Request(this.Urls.m().queryfloor, msgobj).modepost()
				console.log(resdata)
				let arr = []
				for (let i = 0; i < resdata.length; i++) {
					arr.push(resdata[i].title)
				}
				that.tabNav = arr
				that.query(that.tabNav[that.TabCur])
				
			},
			async query(floor){
				let msgobj = {
					floor:floor
				}
				let resdata = await new this.Request(this.Urls.m().queryseatbyfloor, msgobj).modepost()
				this.seatlist = resdata
			},
			todetail(item){
				uni.navigateTo({
					url: `/pages/index/seatcenterdetail?id=${item.id}&floor=${item.floor}&title=${item.title}&detail=${item.detail}`,
				});
			},
			tabSelect(e) {
				this.TabCur = e.currentTarget.dataset.id;
				this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
				this.query(this.tabNav[this.TabCur])
			}
		}
	}
</script>

<style>
	page {
		padding-top: 45px;
	}

	.bg-img {
		width: 150rpx;
		height: 150rpx;
		background-color:#00ff00;
/* 		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column; */
	}
	
	.seattitle{
    width: 150rpx;
    height: 150rpx;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}
	
	.seattitle01{
		font-weight: bold;
	}
	.seattitle02{
		font-size: 0.8em;
		font-weight: bold;
	}
	.seattitle03{
		font-size: 0.7em;
	}
</style>